import React from "react";
import { useLocalStore, useObserver } from "mobx-react";

import { observable, action } from "mobx";
import cobweb from "src/utils/cobweb";
import { error } from "src/utils/tools";

class NodeInvestStore {
  @observable
  data: any[] = ["0","0","0","0"];

  @observable
  canWithdraw:string = "0";
  @observable
  investAmount:string = "0";
  @observable
  investEnd:string = "0";
  @observable
  investStart:string = "0";
  @observable
  withdraw:string = "0";
  @observable
  level0 = "0";
  @observable
  level1 = "0";
  @observable
  level2 = "0";
  @observable
  level3 = "0";
  @observable
  level4 = "0";

  constructor() {
    this.init();
  }

  @action.bound
  async init() {
    try {
      const { cobWeb ,web3} = await cobweb();
      this.data = await cobWeb.methods.nodeInvest().call();
      const data = await cobWeb.methods.getTermInfo(this.data[1]).call();
      this.canWithdraw = web3.utils.fromWei(data["canWithdraw"]);
      this.investAmount = web3.utils.fromWei(data["investAmount"]);
      this.investEnd = data["investEnd"];
      this.investStart = data["investStart"];
      this.withdraw =  web3.utils.fromWei(data["withdraw"]);

      this.level0 = await cobWeb.methods.getVnodeCount(0).call();
      this.level1 = await cobWeb.methods.getVnodeCount(1).call();
      this.level2 = await cobWeb.methods.getVnodeCount(2).call();
      this.level3 = await cobWeb.methods.getVnodeCount(3).call();
      this.level4 = await cobWeb.methods.getVnodeCount(4).call();
    
    } catch (err) {
      error(err);
    }
  }
}

const store = new NodeInvestStore();

const NodeInvest: React.FC = () => {
  const iStore = useLocalStore(() => store);
  return useObserver(() => (
    <>
      <h3>基础信息</h3>
      <p>用户数: {iStore.data[0]}</p>
      <p>当前轮数: {iStore.data[1]}</p>
      <p>此轮开始投资id: {iStore.data[2]}</p>
      <p>总投资比数: {iStore.data[3]}</p>
      <h3>本轮信息</h3>
      <p>可提额: {iStore.canWithdraw}</p>
      <p>已提额: {iStore.withdraw}</p>
      <p>投资开始id: {iStore.investStart}</p>
      <p>投资结束id: {iStore.investEnd}</p>
      <p>入金总额: {iStore.investAmount}</p>
      <h3>等级信息</h3>
      <p>level0: {iStore.level0}</p>
      <p>level1: {iStore.level1}</p>
      <p>level2: {iStore.level2}</p>
      <p>level3: {iStore.level3}</p>
      <p>level4: {iStore.level4}</p>
    </>
  ));
};

export default NodeInvest;
